import React from "react";

interface AutoPostSettingsProps {
  isChecked: boolean;
  setIsChecked: React.Dispatch<React.SetStateAction<boolean>>;
  interval: number;
  setInterval: React.Dispatch<React.SetStateAction<number>>;
}

const AutoPostSettings: React.FC<AutoPostSettingsProps> = ({ isChecked, setIsChecked, interval, setInterval }) => {
  const hours = Array.from({ length: 24 }, (_, i) => i + 1);

  return (
    <div className="flex justify-between w-full bg-gray-100 mt-6">
      <div className="w-1/2 bg-white rounded-lg shadow p-6">
        <label className="flex items-center space-x-3 mb-4">
          <input
            type="checkbox"
            className="form-checkbox h-5 w-5 text-blue-600"
            checked={isChecked}
            onChange={() => setIsChecked(!isChecked)}
          />
          <span className="text-gray-700">Auto posts</span>
        </label>
        <div className="flex flex-col space-y-2">
          <label htmlFor="time-interval" className="text-gray-700">
            Select Time Interval (hours):
          </label>
          <select
            id="time-interval"
            className="form-select mt-1 block w-full py-2 px-3 border border-gray-300 bg-white rounded-md shadow-sm"
            value={interval}
            onChange={(e) => setInterval(Number(e.target.value))}
          >
            {hours.map((hour) => (
              <option key={hour} value={hour}>
                {hour} {hour === 1 ? "hour" : "hours"}
              </option>
            ))}
          </select>
        </div>
      </div>
    </div>
  );
};

export default AutoPostSettings;
